@import url("https://fonts.googleapis.com/css2?family=Open+Sans:300,400,600,700,800&display=swap");

*{
    margin:  0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}

body{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #060a1f;
}
.clock h1{
    position: relative;
    display: block;
    color: #fff;
    text-align: center;
    margin: 10px 0;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.4em;
    font-size: 1em;
}

.clock .time{
    display: flex;
} 
.clock .time div {
    position: relative;
    margin: 0 5px;
    -webkit-box-reflect: below 1px linear-gradient(transparent,#0004);
    /* box-shadow: 2px 1px 10px rgb(0, 0, 0); */
}
.clock .time div span{
    position: relative;
    display: block;
    width: 100px;
    height: 80px;
    background: #2196f3;
    color: #fff;
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3em;
    z-index: 10;
    box-shadow: 0 0 0 1px rgba(0,0,0,.2);
}

.clock .time div span:nth-child(2){
    height: 30px;
    font-size: 0.7em;
    letter-spacing: 500;
    z-index: 9;
    box-shadow: none;
    background: #127fd6;
    text-transform: uppercase;
}
.clock .time div:nth-last-child(2) span{
   
    background: #ff006a;

}
.clock .time div:nth-last-child(2) span:nth-child(2){
   
    background: #ec0062;

}

.clock .time div:nth-last-child(1) span{
    position: absolute;
    bottom: 0;
    width: 60px;
    height: 40px;
    font-size: 1.5em;
    background: #fff;
    color: #000;
    -webkit-box-reflect: below 1px linear-gradient(transparent,#0004);
}